
*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  outline: 0;
  text-decoration: none;
  list-style: none;
  border: none;
}

:root {
  --dark-color:17%;
  --light-color:17%;
  --white-color:10%;
  --primary-color-hue: 252;
  --color-white: hsl(252, 30%, var(--white-color));
  --color-light: hsl(252, 30%, var(--light-color));
  --color-gray: hsl(252, 15%, 65%);
  --color-primary: hsl(var(--primary-color-hue), 75%, 60%);
  --color-secondary: hsl(252, 100%, 90%);
  --color-success: hsl(120, 95%, 17%);
  --color-danger: hsl(0, 95%, 65%);
  --color-dark: hsl(252, 30%, var(--dark-color));
  --color-black: hsl(252, 30%, 10%);
}

html {
    font-size: 80%;
  }
  
  body {
    /* background-color: #151638; */
    background-color: hsl(252, 30%, 17%);
    background-color: var(--color-light);
    overflow-x: hidden;
    color: rgb(0, 0, 0);
    margin: auto;
  
    padding-left: 0px;
    padding-right: 0px;
    font-size: .8rem;
    font-family: "Poppins", sans-serif;
  
  }

/*   .container {
    width: 96%;
    margin: auto;
  
  }
   */
  .row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    text-align: center;
  }
  
  .row.center {
    justify-content: center;
  }
  
  .Box{
     /*  background-color: #0b0d1e; */
     background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.5)), url(/React-Weather-App/static/media/warm-bg.635ed87d.jpg);
     background-color: hsl(252, 30%, 10%);
     background-color: var(--color-white);
      width: 20rem;
      height: 45rem;
     border-radius: 35px;
     padding: 10px;
     position: absolute;
     top: 50%;
     left: 50%;
     -webkit-transform: translate(-50%, -50%);
             transform: translate(-50%, -50%);
     align-items: center;
     box-shadow: 2px 4px rgba(0, 0, 0, 0.5);
  }
  
  .menu{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0.5rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
  .Menu{
    padding: 1px;
    width: 2.8rem;
    height: 2.8rem;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.186);
  }
  span i{
      color: rgb(255, 255, 255);
      font-size: 2rem;
  }
  .barIcon{
    color: rgb(255, 255, 255);
    font-size: 1.5rem;
    padding-top: 7px;
}
.title{
  display: flex;
  justify-content: space-between;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}
.menu h2{
  color: #ffff;
}
.menu .title .locationIcon{
  font-size: 1.2rem;
  color: rgb(255, 0, 0);
}
.main{
  display: block;
  padding-top: 1rem;
  padding-bottom: 1rem;
  align-items: center;
  justify-content: space-between;
}
.toggle{
  font-size: 2rem;
  background-color: #ccc;
  transition: .4s;
  border-radius: 34px;
  width: 50px;
  height: 20px;
}
.switch{
  position: relative;
  display: block;
  width: 50px;
  height: 40px;
  margin: 0 10px;
  background-color: #ccc;
  transition: .4s;
  border-radius: 34px;
 
}
.switch:before{
  content: "";
  width: 40px;
  height: 40px;
  background: #000;
  display: block;
  border-radius: 50%;
}
.slider{
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  
  background-color: #ccc;
transition: .4s;
border-radius: 34px;
}
.icon {
 
  display: inline;
  justify-content: center;
  align-items: center;
  
}

.mid img{
  width: 7rem;
 
}
.mid{
  display: flex;
  align-items: center;
  padding-top: 10px;
  grid-gap: 2rem;
  gap: 2rem;
  margin-left: 20px;
  margin-right: 20px;
  color: #ffff;
}
.search-bar {
  background-color: rgba(255, 255, 255, 0.192);
  padding: 10px;
  border-radius: 15px;
  width: 90%;
 margin-left: auto;
 margin-right: auto;
 display: flex;
 margin-top: 1rem;
 
}

.search-bar form input[type="text"] {
  background: transparent;
  width: 80%;
  margin-left: 1rem;
  font-size: 0.9rem;
  color: rgb(255, 255, 255);
  
}
button{
  background-color: rgba(255, 255, 255, 0);
}
button h5{
  color: white;
  cursor: pointer;
}
.value {
  color: rgba(19, 2, 255, 0.75);
  position: absolute;
  top: -30px;
  margin-top: 30px;
  margin-bottom: 30px;
  line-height: 20px;
}
.center{
  display: flex;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  margin-left: 7.5rem;
  
}
.date{
  padding-top: 10px;
  font-size: 1rem;
  font-weight: 700;
}
.w{
  color: #ffff;
}
.s{
  text-shadow: 2px 2px rgba(50, 50, 70, 0.2);
}
h2{
  color: rgb(0, 0, 0);
  font-size: 1.5rem;
  position: relative
}
h1{
  font-size: 3rem;
  position: relative
}
.temp{
  display: flex;
  align-items: center;
  grid-gap: 5rem;
  gap: 5rem;
  
  margin-top: 10px;
  position: relative;
  border-radius: 15px;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 2rem;
  box-shadow: 2px 3px rgba(0, 0, 0, 0.2);
}
.minValues{
  padding-bottom: 1rem;
}
.conditions img{
  width: 30px;
  margin-top: 2rem;
  
}
.conditions{
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 3rem;
  gap: 3rem;
  color: #ffff;
}
.icon-flex{
  background-color: #ffffff40;
  width: 16rem;
  height: 3rem;
 border-radius: 25px;
 padding-top: 10px;
  display: flex;
 justify-content: center;
 grid-gap: 30px;
 gap: 30px;
margin-left:1.3rem;
 margin-top: 3rem;
 
}
.icon-flex .bottomIcon{
font-size: 1.1rem;
color: #000;
}
